html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.logo {
  height: 40px;
  margin: 10px 0 10px 50px;
}

.nav {
  align-items: center;
  height: 60px;
  margin-right: 50px;
  display: flex;
}

.nav__list {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__list :first-child {
  margin-left: 0;
}

.nav__list :last-child {
  margin-right: 0;
}

.nav__item {
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

